.loop_wrap {
    display: flex;
    width: 100vw;
    height: 300px;
    overflow: hidden;
    z-index:1;
  }
  
  .loop_wrap img {
    width: auto;
    height: 100%;
  }
  @-webkit-keyframes loop {
    0% {
      transform: translateX(100%);
    }
    to {
      transform: translateX(-100%);
    }
  }
  @keyframes loop {
    0% {
      transform: translateX(100%);
    }
    to {
      transform: translateX(-100%);
    }
  }
  
  @-webkit-keyframes loop2 {
    0% {
      transform: translateX(0);
    }
    to {
      transform: translateX(-200%);
    }
  }
  
  @keyframes loop2 {
    0% {
      transform: translateX(0);
    }
    to {
      transform: translateX(-200%);
    }
  }
  .loop_wrap img:first-child {
    -webkit-animation: loop 50s -25s linear infinite;
            animation: loop 50s -25s linear infinite;
  }
  
  .loop_wrap img:last-child {
    -webkit-animation: loop2 50s linear infinite;
            animation: loop2 50s linear infinite;
  }

  /*看板部分*/
.plate{
  width:800px;
  margin-top:30px;
  border-radius:10px;
  border:4px solid rgba(225,225,225,0);
  position:relative;
}
/*文字部分*/
.plate span{
  padding:20px;
  display:block;
  text-align:center;
}


#navy{
  background:#1a405f;
  box-shadow: inset 0 0 0 2px #fff, 0px 5px 5px rgba(0, 0, 0, 0.5);
  color:#fff;
}

.plate:before,
.plate:after{
    content:"";
    width:4px;
    height:50px;
    position:absolute;
    top:-35px;
    box-shadow:inset 0px -1px 1px rgba(0, 0, 0, 0.5),0 0 5px rgba(0, 0, 0, 0.5);
}
.plate:before{
    left:13px;
}
.plate:after{
    right:13px;
}
/*ネイビー*/
#navy:before,
#navy:after{
    background:#fcfcfa;
}

.plate span:before,
.plate span:after{
    content:"";
    border-radius:50%;
    position:absolute;
    top:10px;
    box-shadow:inset 0px -1px 2px rgba(0, 0, 0, 0.5),inset 0px 1px 1px rgba(0, 0, 0, 0.2);
}
/**ネイビー**/
#navy span:before,
#navy span:after{
    width:10px;
    height:10px;
    background:#fcfcfa;
}
#navy span:before{
    left:10px;
}
#navy span:after{
    right:10px;
}
.card-sec {
  padding-top: 60px; 
  padding-bottom: 142px; 
  background-color: #EFEFEF; 
}

.page-title {
  position: relative;
  color: #333333;
  font-size: 60px;
}

.page-title:before { 
  display: inline-block;
  position: absolute;
  width: 76px;
  height: 5px;
  border-radius: 2px;
  background-color: #3F51B5;
  content: '';
  bottom: -42px;
  left: 50%;
  transform: translateX(-50%);
}

.card-container {
  display: grid; 
  grid-template-columns: repeat(auto-fit, minmax(282px, 1fr)); 
  grid-gap: 10px 0; 
  gap: 10px 0; 
  margin: 49px auto 0;
  text-align: start; 
}

.card-item {
  width: 100%;
  max-width: 282px;
